.pokemon {
    border: solid 1px black;
    border-radius: 3px;
    margin: 5px;
    width: 130px;
    background-color: alpha($color: #000000);
    transition: background-color ease 0.3s, width ease 0.3s;
    &:hover {
        background-color: rgba(255, 235, 205, 0.452);
        width: 135px;
    }
}
  
.pokemon__types {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 0;
}

.type {
    list-style: none;
    background-color: rgba(197, 44, 6, 0.432);
    border-radius: 5px;
    margin: 5px;
    padding: 2px;
}

.pokemon__image {
    width: 100px;
}

.pokemon__name {
    font-weight: bold;
    text-transform: capitalize;
    font-size: 20px;
}